import { type ClassValue, clsx } from "clsx";
import { twMerge } from "tailwind-merge";

/**
 * Combina classes CSS com suporte para Tailwind
 * @param {ClassValue[]} inputs - Classes CSS para combinar
 * @returns {string} Classes combinadas
 */
export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

/**
 * Calcula a mdia de um array de nmeros
 * @param {number[]} values - Valores para calcular a mdia
 * @returns {number} Mdia calculada ou 0 se o array estiver vazio
 */
export function calculateAverage(values: number[]): number {
  if (values.length === 0) return 0;
  
  const sum = values.reduce((acc, value) => acc + value, 0);
  return sum / values.length;
}

/**
 * Formata uma nota para exibio
 * @param {number} value - Valor da nota
 * @returns {string} Nota formatada
 */
export function formatGrade(value: number): string {
  // Se o valor for 0 e no houver dados, retorna trao
  if (value === 0) return '-';
  
  // Arredonda para uma casa decimal
  return value.toFixed(1);
}

/**
 * Verifica se uma nota  aprovada, com base na nota mnima
 * @param {number} value - Valor da nota
 * @param {number} passingGrade - Nota mnima para aprovao
 * @returns {boolean} true se a nota for suficiente para aprovao
 */
export function isPassingGrade(value: number, passingGrade: number): boolean {
  return value >= passingGrade;
}

/**
 * Gera uma cor para um ndice especfico
 * til para grficos com mltiplas sries
 * @param {number} index - ndice da cor
 * @returns {string} Cor em formato hexadecimal
 */
export function getColorByIndex(index: number): string {
  const colors = [
    '#4F46E5', // Indigo
    '#F59E0B', // Amber
    '#10B981', // Emerald
    '#EF4444', // Red
    '#6366F1', // Violet
    '#EC4899', // Pink
    '#8B5CF6', // Purple
    '#14B8A6', // Teal
  ];
  
  return colors[index % colors.length];
}

/**
 * Cria um atraso (sleep) em uma funo assncrona
 * @param {number} ms - Tempo de espera em milissegundos
 * @returns {Promise<void>} Promise que resolve aps o tempo especificado
 */
export function delay(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms));
}
